@use "global" as *;

.button {
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: rem(16);
  height: inherit;
  padding: rem(20) rem(40);
  font-size: rem(14);
  font-weight: $medium;
  line-height: 1;
  color: $white;
  // background: $ac-color;
  background: linear-gradient(to bottom,#F1CCCF 0%, $ac-color 100%);
  border: 0.08em solid transparent;
  border-radius: 5em;  
  box-sizing: border-box;
  transition: color 0.3s, background-color 0.3s;
}

.button span{
  width: rem(40);
  height: 0.6em;
  border-bottom: 0.16em solid ;
  border-right: 0.16em solid ;
  transform: translateY(-50%) skew(45deg);
  transform-origin: center;
}

.button:hover {
  color: $ac-color;
  background: $white;
}

.button--green{
  // background: #A9BEA0;
  background: linear-gradient(to bottom,#B8D0B6 0%,#A9BEA0 100%);
}

.button--green:hover{
  color: #A9BEA0;
  border: 0.08em solid #A9BEA0;
}

.button--outline:hover{
  border: 0.08em solid $ac-color;
}